// This is a css class that can be used to style a view component in View Designer
.com-example-test210500-blue-container {
    border: 1px solid blue;
    background-color: blue;
  }
  
  // Tutorial class.
  .com-example-test210500-green-container {
    border: 1px solid green;
    background-color: lawngreen;
  }
  
  // This is an example how to style the rx-rating Adapt component.
  // It is used in the star-rating view component.
  .com-example-test210500-star-rating-red-star {
    // We override the Adapt default color for the star color.
    .a-rating__item {
      color: red;
    }
  }
  
  // It is used in the star-rating view component.
  .com-example-test210500-star-rating-green-star {
    // We override the Adapt default color for the star color.
    .a-rating__item {
      color: greenyellow;
    }
  }
  
  // This class is used to override Adapt panel style, adding a border around the panel.
  .com-example-test210500-override-rx-tab {
    .tab-content {
      border-left: 1px solid $color-dark-grey;
      border-bottom: 1px solid $color-dark-grey;
      border-right: 1px solid $color-dark-grey;
    }
  }
  
  .com-example-test210500-wizard-container {
    height: 750px;
    margin-top: -1rem;
  }
    
  // This is used in the custom-grid view component example where
  // we want to create a custom icon used in view designer.
  // Image original source:
  // https://www.deviantart.com/ladycoventina/art/Kuroneko-Sama-Coloured-136100276
  // Please see the explanation in file "custom-grid-registration.module.ts".
  .d-icon-com-example-test210500-custom-grid-icon {
    background: url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: center;
  }